@import "../../index.scss";

header{
    height: 100vh;
    padding-top: 1rem;
    overflow: hidden;
}

.header-container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* =========== CTA =========== */

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}


/* ======== HEADER SOCIALS ======== */

.header-socials{
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header-socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background-color: $color-primary;
}

/* ========== MY PICTURE ========== */
.me{
    background: linear-gradient($color-primary, transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem;
}

/* =========== SCROLL DOWN =========== */

.scroll-down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}

/* ============ RESPONSIVE ============ */

@media screen and (max-width: 1024px){
    header{
        height: 68vh;

    }
}
  
  @media screen and (max-width: 600px){
    header{
        height: 100vh;
    }

    .header-socials, .scroll-down{
        display: none;
    }
}